<template>
  <!-- Main container for movies section -->
  <section class="container-fluid">
    <div class="movies row" id="movies">
      <!-- Search bar for filtering movies -->
      <div class="movies_searchbar">
        <label for="search"><i class="fas fa-search"></i></label>
        <input type="search" v-model="query" @input="fetchData" class="search" @keypress="onPress" />
      </div>
      <!-- Container for movie items -->
      <div class="col-md-12  movies_items">
        <Movieitem v-for="movie in pages" :key="movie.id" :movie="movie" /> <!-- Rendering movie items from the array -->
      </div>
      <!-- Pagination buttons for switching between pages -->
      <span class="movies_pagnation">
        <button class="fas movies_pagnation_buttons" @click="prevPage">
          &#xf104;
        </button>
        <button class="fas movies_pagnation_buttons" @click="nextPage">
          &#xf105;
        </button>
      </span>
    </div>
    <!-- Container for upcoming movies carousel -->
    <section class="premiers">
      <h3 class="premiers_title">
        <i class="fa-solid fa-play"></i>Скоро на сайте
      </h3>
      <div class="premiers_uncoming">
        <span>
          <Carousel class="carousel" :items-to-show="3.5" :wrap-around="true" :transition="500">
            <Slide v-for="item in uncoming" :key="item"><!-- Rendering upcoming movies in a carousel -->
              <img :src="item.posterUrlPreview" :alt="item.nameRu" />
            </Slide>
          </Carousel>
        </span>
      </div>
    </section>
  </section>
</template>

<script>
import Movieitem from "../src/components/MovieItem.vue";
import axios from "axios";
import { defineComponent } from 'vue'
import 'vue3-carousel/dist/carousel.css'
import { Carousel, Slide } from 'vue3-carousel'



export default defineComponent({
  name: 'AutoPlay',
  components: {
    Movieitem,
    Carousel,
    Slide,

  },
  data() {
    return {
      currentPage: 3,
      pages: [],  /* Array of pages */
      search: "",
      query: "",
      uncoming: [''], // Array of upcoming movies
    }
  },

  created() {
    this.fetchData(); //Updating movie content from Api 
  },
  methods: {
    // Api links for receiving and searching movies 
    async fetchData() {
      let config = {
        // Set headers for the API request
        headers: {
          accept: "application/json",
          // "X-API-KEY": "f833fc52-ca59-4cb8-b1ed-f34f0ab9e9e9",
          // "X-API-KEY": "ba5fe254-f596-4c30-9953-e7d2602e8f94",
          // "X-API-KEY": "1468dc37-6ae5-44b0-a581-e70793163c42",
          "X-API-KEY": "0b2dba66-204b-4e4b-b4ec-9d377f8fb0cb",
          // "X-API-KEY": "4cf6d416-6526-4581-aa8d-c4187567a439",
          // "X-API-KEY": "c1bd8170-db03-406f-9402-cb2a383a7b00",
          // "X-API-KEY": "42195fdc-8413-4121-a264-cf7018df02df",

        },
      };
      // Get top rated movies
      let filmUrl = await axios.get(
        "https://kinopoiskapiunofficial.tech/api/v2.2/films/top?type=TOP_250_BEST_FILMS&page=" +
        this.currentPage,
        config
      );
      // Get upcoming movies
      let uncoming_films = await axios.get(
        "https://kinopoiskapiunofficial.tech/api/v2.2/films/premieres?year=2023&month=MARCH",
        config
      );
      // Get movies based on search query
      let filmSearch = await axios.get(
        `https://kinopoiskapiunofficial.tech/api/v2.1/films/search-by-keyword?keyword=${this.query}&page=` +
        this.currentPage,
        config
      );
      // Update state with fetched data
      this.uncoming = uncoming_films.data.items.splice(0, 10);
      let filmCount = filmUrl.data.films;
      let filmSearchData = filmSearch.data.films;
      if (this.query !== '') {
        this.pages = filmSearchData.splice(1, 18);
      } else {
        this.pages = filmCount.splice(1, 18);
      }
    },

    // Pagination for switching between pages
    prevPage() {
      // Decrement current page, or go to last page
      if (this.currentPage !== 3) {
        this.currentPage--;
      } else {
        this.currentPage = 13;
      }
      // Fetch data for the new page
      this.fetchData();
    },
    nextPage() {
      // Increment current page, or go to first page
      if (this.currentPage !== 13) {
        this.currentPage++;
      } else {
        this.currentPage = 3;
      }
      // Fetch data for the new page
      this.fetchData();
    },

    // Refresh data when the escape key is pressed
    onPress(e) {
      if (e.key === "Escape") {
        this.fetchData();
      }
    },
  },
});
</script>

<style>
.line {
  border: 0.06em solid red;
}

.movies {
  font-family: Caldina;
  margin-bottom: 6.2em;
}

.movies_searchbar {
  text-align: center;

  position: relative;
  cursor: pointer;
  margin-bottom: 3.1em;
}

.fa-search {
  cursor: pointer;
  margin-right: 1.2em;
  position: absolute;
  bottom: 0.6em;
}

.search {
  height: 1.8em;
  border: transparent;
  outline: 0;
  background: transparent;
  border-bottom: 0.06em solid #e5e5e5;
  color: #bbbbbb;
  font-size: clamp(0.9em, 1.2cqw, 1.5em);
  padding-left: 1.2em;
}

.movies_items {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.premiers_uncoming {
  display: flex;
  justify-content: center;
  text-align: center;
}

.premiers_uncoming h4 {
  margin-top: 0.6em;
}

.premiers img {
  width: clamp(9.5em, 40cqw, 19em);
  height: clamp(12.5em, 55cqw, 28em);
  cursor: pointer;
  margin: 0 2em;
  border-radius: .8cqw;
}

.premiers_title {
  padding: 0.3em;
}

.fa-play {
  margin: 0.9em;
}

.movies_pagnation {
  display: flex;
  justify-content: center;
}

.movies_pagnation_buttons {
  cursor: pointer;
  font-size: clamp(0.6em, 3cqw, 1.5em);
  border: 0.06em #bbbbbb;
  border-radius: 50%;
  padding: 0.6em 0.9em;
  color: #000000;
  margin: 0em 0.6em;
}

.movies_pagnation_buttons:hover {
  background-color: #000000;
  color: #bbbbbb;
}

.movies_pagnation_buttons:active {
  box-shadow: 0.06em 0.06em 0.3em #000000;
}

@media (min-width: 106em) {
  .movieRow {
    max-width: 106em;
  }
}

@media (max-width:56em) {
  .premiers img {
    margin: 0 1.5em;
  }

  .movies_searchbar {
    transition: ease all 1s;
    margin-bottom: 1.2em;
  }
}
</style>


