<template>
  <router-link :to="`/${movie.filmId}`" target='_blank' class="movie">
    <img :src="posterImg" class="movie_img" alt="" />
    <span class="movie_name">{{ filmName }}</span>
    <span class="movie_genre">{{ filmGenre }}</span>
  </router-link>
</template>

<script>
export default {
  /* This props taking movie information from Movies page */
  props: {
    movie: {
      required: true,
      ref: String,
    },
  },
  computed: {
    /* Exporting from movie, informaton for our movie item */
    posterImg() {
      return this.movie.posterUrl;
    },
    filmName() {
      return this.movie.nameRu;
    },
    filmGenre() {
      let genresList = this.movie.genres.map((items) => items["genre"]);
      let genre = genresList.splice(0, 2);
      return genre.toString();
    },
  },
};
</script>
<style>
.movie {
  margin: 1em .5em;
  display: flex;
  flex-direction: column;
  color: #ffffff;
  text-decoration: none;
  font-family: Caldina;
  transition: all 0.4s ease-in-out;
}

.movie_img {
  width: clamp(9em, 25cqw, 17em);
  height: clamp(12em, 35cqw, 24em);
  border-radius: .8cqw;
 
}

.movie:hover {
  opacity: 0.4;
  transform: translateY(-1em);
}

.movie_name {
  width: clamp(5em, 20cqw, 12em);
  font-size: clamp(1em, 1.2cqw, 1.5em);
  font-weight: 600;
}

.movie:hover {
  color: #ffffff;
}

.movie_genre {
  width: clamp(3em, 5cqw, 9.3em);
  font-size: clamp(.8em, 1.2cqw, 1.2em);
  color: #bbbbbb;
}
</style>

 






