<template>
  <!-- Agile slider   -->
  <agile :options="myOptions">
    <div class="slide_film" v-for="(item, index) in content" :key="index.id" :index="index">
      <router-link :to="`/${item.filmId}`" target="_blank">
        <!-- Adding film information for a first page -->
        <div class="slide_film_info">
          <p class="slide_film_name">{{ item.name }}</p>
          <ul class="slide_film_data">
            <li>{{ item.year }}</li>
            <li>{{ item.filmLength }}</li>
            <li>{{ item.rating }}</li>
          </ul>
          <span>Режиссёр: {{ item.staff }}</span>
          <span>Актёры: {{ item.actors }}</span>
          <a class="slide_film_watch">Смотреть</a>
        </div>
        <div class="slide_film_video">
           <!-- Adding video to slider -->
          <video class="slide_film_video_content" :src="item.video" autoplay muted loop></video>
        </div>
      </router-link>
    </div>
  </agile>
</template>

<script>
import { VueAgile } from "vue-agile";

export default {
  name: "MovieSlider",
  components: {
    agile: VueAgile,
  },
  data() {
    return {
      // Slider movie content video,film etc....
      content: [
        {
          video: require('../assets/avatar.mp4'),
          // video: "https://www.youtube.com/embed/S4R0bW9DMRs?rel=0&autoplay=1&mute=1&loop=1&controls=0&vq=hd720",
          filmId: 505898,
          year: "2022",
          filmLength: "3 ч 12 мин",
          rating: "7.9",
          name: "Аватар: Путь воды",
          staff: "Джеймс Кэмерон",
          actors: "Сэм Уортингтон, Зои Салдана, Сигурни Уивер, Вин Дизель"
        },
        {
          video: require('../assets/morbius.mp4'),
          // video: "https://www.youtube.com/embed/OrSrw_uPyIk?rel=0&autoplay=1&mute=1&loop=1&controls=0&vq=hd720", iframe for youtube  link
          filmId: 1100425,
          year: "2022",
          filmLength: "1 ч 44 мин",
          rating: "5.6",
          name: "Морбиус",
          staff: "Даниэль Эспиноса",
          actors: "Джаред Лето, Мэтт Смит, Адриа Архона, Джаред Харрис"
        },


        {
          video: require('../assets/blackadam.mp4'),
          // video: "https://www.youtube.com/embed/NWgldtvzsDk?rel=0&autoplay=1&mute=1&loop=1&controls=0&vq=hd720",
          filmId: 1009017,
          year: "2022",
          filmLength: "1 ч 53 мин",
          rating: "6.1",
          name: "Чёрный Адам",
          staff: "Жауме Кольет-Серра",
          actors: "Дуэйн Джонсон, Элдис Ходж, Пирс Броснан, Ной Сентинео"
        },
      ],
      //Slider options
      myOptions: {
        dots: false,
        fade: true,
        navButtons: true,
        infinite: true,
        centerMode: true,
        slidesToShow: 1,
      },
    };
  },
};
</script>

<style>
*,
body,
.agile {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.slide_film_info {
  font-family: Caldina;
  position: absolute;
  font-size: 1.8em;
  color: #ffffffb3;
  z-index: 10;
  bottom: 5em;
  left: 4em;
  display: flex;
  flex-direction: column;
}

.slide_film_info p {
  font-weight: 900;
  margin-left: 35px;
  font-size: clamp(1em, 4cqw, 2.5em);
  margin-bottom: 0em;
}

.slide_film_info span {
  text-decoration: underline dotted;
  text-underline-offset: 0.3em;
  text-decoration-color: #ff00006a;
  font-size: clamp(0em, 1.2cqw, 1.1em);
  margin-left: 35px;
}


.slide_film_info ul {
  display: inline-flex;
  margin-right: 3.7em;
}

.slide_film_info ul li {
  margin-right: 1em;
  font-size: clamp(0em, 1.2cqw, 1.1em);
}

.slide_film_watch {
  margin: .8em .5em 0em 1.1em;
  padding: 0.1em 0.1em;
  background-color: #ff0000;
  border:.2em solid #ff0000;
  max-width: 5em;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: .8em;
  color: #ffffffb3;
  font-weight: 700;
  box-shadow: 0em .1em .5em #ff0000;
  border-radius: .8em;
  transition: all .3s linear;
}

.slide_film_watch:hover {
  color: #ffffffb3;
  opacity: .8;
}



.slide_film_data li {
  padding: 0;
}

.slide_film_data li {
  font-size: clamp(.5em, 1.2cqw, 1.5em);
  ;
}

.slide_film_data li:last-child {
  padding: 0 0.3em;
  border-radius: 0.1em;
  background-color: #2f2c2c;
}

.slide_film_video {
  position: relative;
  aspect-ratio: 18/10;
  box-shadow: 8cqb 10cqb 5cqb #2f2c2c inset, -13cqb -23cqb 10cqb #2f2c2c inset;
}

.slide_film_video_content {
  position: relative;
  z-index: -1;
}

.slide_film_video>video {
  position: absolute;
  width: 100%;
  height: 100%;
}


.agile__actions {
  position: absolute;
  top: 40%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.agile__nav-button {
  position: relative;
  z-index: 15;
  background-color: transparent;
  border: none;
  cursor: pointer;

  font-size: clamp(1em, 3cqw, 4em);
  padding-left: 0.6em;
  padding-right: 0.6em;
 visibility: hidden;
}


.agile__nav-button--prev::before {
  content: "\2039";
visibility: visible;
color: #ffffffb3;
display: block;
}

.agile__nav-button--next::before {
  content: "\203A";
  display: block;
  visibility: visible;
  color: #ffffffb3;


}

.agile__nav-button:hover::before {
  color: rgba(255, 0, 0, 0.381);
}

@media (max-width:80em) {





  .slide_film_info {
    bottom: 2.2em;
    left: 3.3em;
    transition: ease all .5s;
  }

  .slide_film_video {
    transition: ease all .5s;
    box-shadow: 0cqb 7cqb 3cqb #2f2c2c inset, 0cqb -10cqb 7cqb #2f2c2c inset;
  }

  .slide_film_watch {
    margin: .8em .5em 0em 1.5em;
    transition: ease all .5s;
    font-size: .7em;
  }

  .slide_film {
    margin-bottom: 6.2em;
    transition: ease all .5s;
  }
}

@media (max-width:56em) {
  .slide_film_info ul {
    transition: ease all .5s;
    display: none !important;
  }

  .slide_film_info span {
    transition: ease all .5s;
    display: none !important;
  }

  .slide_film_watch {
    padding: .2em .4em;
    transition: ease all .5s;
    font-size: .7em;
    margin: 0em .1em 0em 1.6em;
  }

  .slide_film {
    margin-bottom: 3.1em;
    transition: ease all .5s;
  }
}

@media (max-width:46em) {
  .slide_film_video {
    transition: ease all .5s;
    box-shadow: 0cqb 0cqb 3cqb 4cqb #2f2c2c inset;

  }

  .slide_film_watch {
    display: none !important;
    transition: ease all .5s;

  }

  .slide_film_info {
    transition: ease all .5s;
    bottom: 1em;
    left: .5em;
    font-size: 1.5em;
  }

  .slide_film {
    margin-bottom: 1.2em;
    transition: ease all .5s;
  }
}
</style>
