<template>
  <!-- Filmpage section contains information about single film and show similar films -->
  <section class="container-fluid">
    <div class="row film_page">
      <span class="col-auto">
        <!-- Film image -->
        <img :src="film.posterUrl" alt="" srcset="" class="film_page_img" />
      </span>
      <!-- Film informaton -->
      <span class="col-md-7">
        <h1 class="film_page_name">{{ film.nameRu }}</h1>
        <ul class="film_page_info">
          <li>{{ film.ratingKinopoisk }}</li>
          <li>{{ film.year }}</li>
          <li v-if="film.genres">{{ film.genres[1].genre }}</li>
          <li v-if="film.countries">{{ film.countries[0].country }}</li>
        </ul>
        <ul class="film_page_description">
          <li>{{ film.slogan }}.</li>
          <li>О фильме: {{ film.description }}</li>
        </ul>
        <div class="film_page_staff">
          <span>Режиссёр: {{ staff.nameRu }}</span>
          <span class="actor_names">Актёры: {{ actors.join(", ") }}</span>
        </div>
      </span>
      <!-- Film video -->
      <div class="film_page_iframe">
        <h3>Смотреть {{ film.nameRu }} в HD 720-1080 хорошем качестве</h3>
        <iframe :src="film_video" controls allowfullscreen>
          Your browser does not support the video tag.
        </iframe>
      </div>
    </div>
    <!-- Similar films -->
    <section class="film_page_similar">
      <h3>Похожие</h3>
      <div class="film_page_similar_movie">
        <div v-for="item in similiarMovies" :key="item.id">
          <router-link :to="`/${item.filmId}`" target="_blank" class="movie">
            <img :src="item.posterUrl" class="movie_img" alt="" />
            <span class="movie_name">{{ item.nameRu }}</span>
          </router-link>
        </div>
      </div>
    </section>
    <hr size="5" color=" #bbbbbb" style="margin: 3.1em auto" />
    <div class="row">
      <!-- Comments form, for left comments about the film-->
      <section class="col-md-6  comments_area">
        <h4 class="comments_area_title">
          Оставь свой отзыв <i class="fa-sharp fa-regular fa-message"></i>
        </h4>
        <form action="" class="form_groups">
          <div class="form form_groups_one">
            <input type="text" name="name" id="name" class="form_input input-label" required placeholder="Имя"
              autocomplete="off" v-model="comment.name" />
            <label for="name" class="form_label input-label" id="label">Имя</label>
          </div>
          <div class="form form_groups_three">
            <textarea id="story" class="text_form" name="story" placeholder="Комментарии"
              v-model="comment.text"></textarea>
            <label for="story" class="label">Комментарии</label>
          </div>
          <button id="form_button" @click="inputData">Оставить</button>
        </form>
      </section>
      <!-- Comments secton, that users has left about the film-->
      <section class="col-md-5 comments_section">
        <h4 class="comments_section_title">Комментарии</h4>
        <div class="comments_section_comments" v-for="item in commentData" :key="item.id">
          <span class="comments_section_commentator">
            <p>{{ item.name }}</p>
          </span>
          <p class="comments_section_text">{{ item.text }}</p>
          <p class="comments_section_date">{{ item.date }}</p>
        </div>
      </section>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  name: "FilmPage",
  components: {},
  data() {
    return {
      film: "",
      film_video: [],/*  Array of film video */
      comment: [], /* Array of massages */
      commentData: [],
      similiarMovies: [],
      staff: "",
      actors: [],
    };
  },
  created() {
    this.MovieFetch();
    this.getData();
  },
  mounted() {
    window.scrollTo(0, 0);
  },
  methods: {
    /* Api links for film information,film video and similar movies  */
    async MovieFetch() {
      let config = {
    /*    Set headers for the API request */
        headers: {
          accept: "application/json",
          // "X-API-KEY": "f82316c5-da7c-4940-a57e-48bc4ffe6ac8",
          "X-API-KEY": "f833fc52-ca59-4cb8-b1ed-f34f0ab9e9e9",
          // "X-API-KEY": "ba5fe254-f596-4c30-9953-e7d2602e8f94",
          // "X-API-KEY": "1468dc37-6ae5-44b0-a581-e70793163c42",
          // "X-API-KEY": "0b2dba66-204b-4e4b-b4ec-9d377f8fb0cb",
          // "X-API-KEY": "4cf6d416-6526-4581-aa8d-c4187567a439",
          // "X-API-KEY": "42195fdc-8413-4121-a264-cf7018df02df",
        
        },
      };
      /* Api links */
      let page_response = await axios.get(
        `https://kinopoiskapiunofficial.tech/api/v2.2/films/${this.$route.params.id}`,
        config
      );
      let video_response = await axios.get(
        `https://kinopoiskapiunofficial.tech/api/v2.2/films/${this.$route.params.id}/videos`,
        config
      );
      let similar_film_response = await axios.get(
        `https://kinopoiskapiunofficial.tech/api/v2.2/films/${this.$route.params.id}/similars`,
        config
      );
      let staff_response = await axios.get(
        `https://kinopoiskapiunofficial.tech/api/v1/staff?filmId=${this.$route.params.id}`,
        config
      );
       /* Update state with fetched data */
      this.staff = staff_response.data[0];
      let actor_names_response = staff_response.data.map((item) => item.nameRu);
      this.actors = actor_names_response.slice(1, 5);
      this.film = page_response.data;
      let video_url = video_response.data.items;
      let video_links = video_url.map((element) => element.url);
      let youtube_link = video_links.find((element) =>
        element.startsWith("https://www.youtube.com")
      );
      this.similiarMovies = similar_film_response.data.items.slice(0, 5);
      this.film_video = youtube_link
        .replace("watch?v=", "embed/")
        .replace("v/", "embed/");
    },
    
    inputData() {
      /* Push a new comment object to commentData array  */
      this.commentData.push({
        name: this.comment.name,
        date: new Date().toISOString().slice(0, 10),
        text: this.comment.text,
      });
      // Clear the input field
      localStorage.setItem(
        `${this.$route.params.id}`,
        JSON.stringify(this.commentData)
        );
        this.comment = "";
    },
    /* Extracting data from local storage by film ID, if there are is no data give empty array */      
    getData() {
      this.commentData = JSON.parse(
        localStorage.getItem(`${this.$route.params.id}`) || "[]" 
      );
    },
  },
};
</script>

<style>
.film_page {
  font-family: Caldina;
  padding: 7em 0em 0em 2em;
  color: #ffffffb3;
  font-size: clamp(1em, 1.2cqw, 1.5em);
}

.film_page_img {
  width: clamp(6.2em, 35cqw, 15em);
  height: clamp(9em, 45cqw, 22em);
  border-radius: 0.6em;
  margin: 0 auto;
}



.film_page_name {
  font-size: clamp (1em, 10cqw, 3.4em);
  margin-left: .8em;
  font-weight: 600;
}

.film_page_info li {
  display: inline-block;
  margin-right: 1.2em;
}

.film_page_info li:first-child {
  border-radius: 0.3em;
  padding: 0 0.3em;
  background-color: #12b77c;
}

.film_page_description li {
  display: block;
  

  font-size: clamp(1em, 1.2cqw, 2em);
}

.film_page_staff {
  padding-left: 1.5em;
  text-decoration: underline dotted;
  text-underline-offset: 0.2em;
  text-decoration-color: #ff00006a;
  font-size: clamp(1em, 1.2cqw, 2em);
}

.actor_names {
  display: flex;
}



.film_page_iframe h3 {
  padding: 3.1em 0 1.2em 0;
}


iframe {
  width: 70%;
  min-height: 30em;
  border: 1px solid;
}

.film_page_similar {
  font-family: Caldina;
  padding: 2em 0.1em 2em 2em;

}

.film_page_similar h3 {
  margin-left: .9em;
}

.film_page_similar_movie {
  display: flex;
  flex-wrap: wrap;
 
}


.comments_area_title {
  padding: 0em 0em 0em 1.2em;
  color: #ffffffb3;
}

.comments_section {
  font-family: Caldina;
  
}

.comments_section_title {
  padding: 0em 0em 1em 2em;
  color: #ffffffb3;
}

.comments_section span {
  font-family: Caldina;
  
  font-size: 1.5em;
  padding-left: 2em;
}


.comments_section_text {
  font-size: 1em;
  padding-left: 3em;
}

.comments_section_date {
  font-size: 0.8em;
  padding-left: 3.7em;
}


.comments_section_commentator {
  font-size: 1.2em;
  font-weight: 500;
  color: #ff00005f;
  display: flex;
}

.comments_area {
  font-family: Caldina;

  transition: all 0.5s ease-in-out;
  margin: 0 1em;
  padding-bottom: 2em;
}

@media (max-width: 80em) {


  .film_page_img {
    width: clamp(10em, 35cqw, 20em);
    height: clamp(18em, 45cqw, 25em);
  }
  .film_page_staff{
    padding-left: 2em;
  }
  .film_page span:nth-child(2) {
    max-width: 100%;
  }

  iframe {
    width: 95%;
  }
}

@media (max-width: 55em) {
  .film_page {
    padding: 6em 0em 0em 0em;
    transition: ease all .5s;

    place-content: center;
  }

  .film_page_img {
    width: clamp(15em, 40cqw, 18em);
    height: clamp(20em, 50cqw, 30em);
    transition: ease all .5s;
    margin: 0 auto;
    transition: ease all .5s;

  }

  .film_page span:nth-child(2) {
    width: 100%;
  }
  .film_page_iframe h3 {
    transition: ease all .5s;

    padding: 3.1em 1em 1.2em 1em;
}
.film_page_iframe iframe {
  transition: ease all .5s;

  padding: 0 1em 1.2em 2em;
}

}

@media (max-width: 45em) {
  .film_page {
    padding: 3em 0em 0em 0em;
    transition: ease all .5s;

  }
}</style>
